<template>
  <div class="flexbox container">
    <div class="section top"></div>
    <div class="section main">
      <div v-for="(line, index) in message" class="section line">
        <div :class="`animated ${line.animation}`" id="message" v-if="getShow(index)">
          {{ line.text }}
        </div>
      </div>
    </div>
    <div class="section bottom" @click="next">Next</div>
  </div>
</template>

<script>
  import messages from './assets/messages.js'

export default {
  data () {
    return {
      i: 0,
      m: 0
    }
  },
  computed: {
    message () {
      return messages[this.m]
    },
  },
  methods: {
    next () {
      this.i += 1
      if (this.i > 4) {
        this.i = 0
        this.m += 1
      }
    },
    getShow (index) {
      if (this.i > index) {
        return true
      }
      return false
    },
    getStyle (index) {
      if (this.i > index) {
        return { 'color': 'grey' }
      } else {
        return { 'color': 'black' }
      }
    }
  }
}
</script>

<style src="animate.css/animate.min.css"></style>
<style>
html, body {
  height: 100%;
  margin: 0;
}

.flexbox.container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
}

.flexbox.container > div {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.line {
  font-size: 1.5em;
  flex: 1;
  justify-content: center;
  text-align: center;
}

.top {
  flex: 5;
}

.main {
  flex: 10;
}

.section {
  /*border: 1px dotted black;*/
}

.bottom {
  font-size: 1em;
  flex: 7;
  text-align: center;
}

#message { 
  position: fixed; 
  width: 100%;
}
</style>



